import { Grid } from "@mui/material";
import React from "react";
import "./Info.css";
import infoImage from "../assets/info_image.png";

function Info() {
  return (
    <div id="info">
      {/* <Navbar></Navbar> */}
      <Grid
        container
        spacing={4}
        direction="row"
        justifyContent="center"
        alignItems="center"
        sx={{ padding: "100px 0" }}
      >
        <Grid
          item
          container
          position="relative"
          md={6}
          sm={12}
          justifyContent="center"
          alignItems="center"
        >
          <Grid item>
            <div className="info_content_image_backgound">
              <img src={infoImage} alt="" />
            </div>
          </Grid>
        </Grid>
        <Grid item md={6} sm={6} xs={8} container direction="column">
          <Grid item>
            <p className="info_text white">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
              enim ad minim veniam, quis nostrud exercitation ullamco laboris
              nisi ut aliquip ex ea commodo consequat.
            </p>
          </Grid>
          <Grid item>
            <p className="info_readMore">Read More</p>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}

export default Info;
