import React, { useEffect, useState } from "react";
import "./Navbar.css";
import {
  Divider,
  Grid,
  IconButton,
  Link,
  List,
  ListItem,
  SwipeableDrawer,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import { makeStyles } from "@mui/styles";

const navigationLinks = [
  { name: "Home", href: "" },
  { name: "About", href: "" },
  { name: "Gallery", href: "" },
  { name: "Resume", href: "" },
  { name: "Contact", href: "" },
];

const useStyles = makeStyles((theme) => ({
  navbarContent: {
    justifyContent: "center",
    [theme.breakpoints.down("md")]: {
      justifyContent: "flex-end",
    },
  },
}));

function Navbar() {
  const classes = useStyles();
  // const styles = useStyles();
  const [open, setOpen] = useState(false);

  function checkTop() {
    var component = document.getElementById("navbar");
    var distanceToTop = component.getBoundingClientRect().top;
    if (distanceToTop <= 0) {
      component.classList.add("sticky");
    }
  }

  useEffect(() => {
    function watchScroll() {
      window.addEventListener("scroll", checkTop);
    }
    watchScroll();

    // Cleaner function
    return () => {
      window.removeEventListener("scroll", checkTop);
    };
  }, []);

  return (
    <div id="navbar">
      <Grid container alignItems="center" className={classes.navbarContent}>
        <Grid item sx={{ display: { xs: "none", md: "block" } }}>
          {navigationLinks.map((item) => (
            <Link
              href={item.href}
              sx={{
                color: "white",
                paddingRight: "2.5%",
                fontSize: "24px",
                fontWeight: 400,
              }}
              variant="button"
              underline="none"
            >
              {item.name}
            </Link>
          ))}
        </Grid>
        <Grid item sx={{ display: { md: "none", xs: "block" } }}>
          {/* <h1 style={{ marginRight: "auto" }}>Rajesh Ganesh Sharma</h1> */}
          <IconButton
            className={classes.navbarMenuIcon}
            sx={{ color: "white" }}
          >
            <MenuIcon
              sx={{ color: "white", height: "35px", width: "35px" }}
              onClick={() => setOpen(true)}
            ></MenuIcon>
          </IconButton>
        </Grid>
      </Grid>

      <SwipeableDrawer
        sx={{ position: "absolute" }}
        open={open}
        anchor="right"
        onOpen={() => setOpen(true)}
        onClose={() => setOpen(false)}
      >
        <div>
          <IconButton>
            <ChevronRightIcon onClick={() => setOpen(false)}></ChevronRightIcon>
          </IconButton>
        </div>
        <Divider></Divider>
        <List>
          {navigationLinks.map((item) => (
            <ListItem>
              <Link
                href={item.href}
                sx={{
                  color: "#0d2427",
                  fontSize: "16px",
                  fontWeight: 400,
                }}
                variant="button"
                underline="none"
              >
                {item.name}
              </Link>
            </ListItem>
          ))}
        </List>
        {/* Drawer */}
      </SwipeableDrawer>
    </div>
  );
}

export default Navbar;
