import { Grid, Link } from "@mui/material";
import React from "react";
import "./Home.css";
import heroImage from "../assets/home_heroImage.png";

function Home() {
  return (
    <div id="home">
      <img src={heroImage} alt="" className="hero_image" />
      <div className="home_content">
        <div className="home_right">
          <div className="home_content_header">
            <h1 className="white">RAJESH GANESH SHARMA</h1>
          </div>
          <div className="logos white ">
            <div className="home_logos">
              <Grid container justifyContent="space-between">
                <Link href="#">
                  <i className="fa-brands fa-facebook-f home_icon "></i>
                </Link>
                <Link href="#">
                  <i className="fa-brands fa-linkedin-in home_icon"></i>
                </Link>
                <Link href="#">
                  <i className="fa-brands fa-instagram home_icon"></i>
                </Link>
                <Link href="#">
                  <i className="fa-brands fa-github home_icon"></i>
                </Link>
              </Grid>
            </div>
          </div>
        </div>
      </div>
      <div className="home_bottom"></div>
    </div>
  );
}

export default Home;
