import React from "react";
import "./Gallery.css";
import { Grid } from "@mui/material";
import { makeStyles } from "@mui/styles";
import galleryPic1 from "../assets/gallery/gallery_pic1.png";
import galleryPic2 from "../assets/gallery/gallery_pic2.png";
import galleryPic3 from "../assets/gallery/gallery_pic3.png";
import galleryPic4 from "../assets/gallery/gallery_pic4.png";
import galleryPic5 from "../assets/gallery/gallery_pic5.png";
import galleryPic6 from "../assets/gallery/gallery_pic6.png";
import galleryVector from "../assets/gallery/gallery_vector.svg";

const imageList = [
  { type: "image", content: galleryPic1 },
  { type: "image", content: galleryPic2 },
  {
    type: "text",
    content:
      "Lorem ipsum dolor sit amet, consec adipiscing elit, sed do eiusmod tempo",
  },

  { type: "image", content: galleryPic3 },
  { type: "image", content: galleryPic4 },
  {
    type: "text",
    content:
      "Lorem ipsum dolor sit amet, consec adipiscing elit, sed do eiusmod tempo",
    color: "#ffffff",
  },
  { type: "image", content: galleryPic5 },
  { type: "image", content: galleryPic6 },
];

const useStyles = makeStyles((theme) => ({
  galleryTextContainer1: {
    height: "250px",
  },
  galleryTextContainer2: {
    height: "250px",
    backgroundColor: "#05595B",
  },
  galleryContainer: {},
}));

function Gallery() {
  const classes = useStyles();
  return (
    <div>
      <div id="gallery">
        <Grid
          container
          spacing={5}
          lg={11}
          // md={8}
          sm={10}
          justifyContent="center"
          alignItems="center"
        >
          {imageList.map((i) => (
            <Grid
              item
              xs
              container
              direction="row"
              justifyContent="center"
              alignItems="center"
              className={classes.galleryContainer}
            >
              {i.type === "image" ? (
                <img className="gallery_image" src={i.content} alt="" />
              ) : (
                <Grid
                  item
                  container
                  justifyContent="center"
                  alignItems="center"
                  className={
                    i.color
                      ? classes.galleryTextContainer2
                      : classes.galleryTextContainer1
                  }
                >
                  <p className="gallery_text">{i.content}</p>
                </Grid>
              )}
            </Grid>
          ))}
        </Grid>
      </div>
      <div className="gallery_bottom">
        <img src={galleryVector} alt="" style={{ width: "100%" }} />
      </div>
    </div>
  );
}

export default Gallery;
